<template>
  <div class="w-full border-b hover:bg-lightest flex flex-col">
    <!-- Retweet header -->
    <div class="pt-4 pl-4 flex flex-row">
      <div class="w-12 mr-4 flex justify-end">
        <i class="text-sm pt-1 fas fa-retweet text-dark"></i>
      </div>
<!--      <a> RT:::::{{ JSON.stringify(tweet) }}</a>-->
      <p class="text-sm text-dark">{{ label }}</p>
    </div>

    <Tweet :tweet="tweet" />
  </div>
</template>

<script>
import Tweet from "@/components/Tweet2.vue";
import {mapActions} from "vuex";

export default {
  name: "Retweet",
  props: ["tweet"],
  components: {
    Tweet: Tweet,
  },
  methods: {
    ...mapActions("warpnet", [
      "getOwnerProfile"
    ]),
  },
  data() {
    return {
      profile: {},
      label: "None"
    };
  },
  async created() {
    try {
      this.profile = await this.getOwnerProfile();
      if (this.tweet.retweeted_by === this.profile.id) {
        this.label = "You Retweeted";
      } else {
        this.label = `${this.tweet.username} Retweeted`;
      }
    } catch (e) {
      console.error("Failed to get profile:", e);
    }
  },
};
</script>
